  padding: 0;
  margin: 0;
  box-sizing: border-box;
  user-select: none;
}

:root {
  --bg-color: #113344;
  --input-header-color: rgba(255, 255, 255, 0.8);
}

.teko {
  font-family: "Teko", serif;
  font-weight: 400px;
  font-style: normal;
}

.geo {
  font-family: "Geo", serif;
  font-weight: 400px;
  font-style: normal;
}

body {
  width: 100vw;
  height: 100vh;
  background-color: var(--bg-color);
  display: grid;
  overflow: hidden;
  grid-template-rows: 5% 95%;
}

header {
  display: grid;
  grid-row: 1 / 2;
  grid-template-columns: 15% 85%;
  grid-template-rows: 100%;
  border: 1px solid gray;
  height: 100%;
  font-family: "Geo", serif;
  font-size: 1.5em;
  letter-spacing: 1px;
  color: white;
}

#left-nav {
  display: flex;
  grid-row: 1 / 2;
  justify-content: space-between;
  align-items: center;
  height: 100%;
  overflow: hidden;
}

#username-container {
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  height: 100%;
  width: 80%;
  border-right: 1px dashed gray;
  background-color: rgba(55, 90, 120, 0.7);
  position: relative;
}

#username-input {
  display: flex;
  z-index: 1;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  font-family: "Geo", serif;
  font-size: 1.5em;
  letter-spacing: 1px;
  color: white;
  height: 80%;
  width: 80%;
  border: none;
  background-color: transparent;
  position: relative;
  outline: none;
}

#scan-lines {
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: -1;
  animation: stretch 3s ease-in-out infinite;
  transform-origin: center;
}

#right-nav {
  grid-column: 2 / 3;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  height: 100%;
  padding-right: 1em;
}

.main-nav-icon {
  display: flex;
  height: 75%;
  stroke: gray;
}

#cog {
  stroke: black;
  fill: gray;
}

#power {
  stroke: green;
}

#content {
  grid-row: 2 / 3;
  display: flex;
  height: 100%;
  background-color: black;
}

#sidebar {
  display: flex;
  justify-content: flex-start;
  height: 100%;
  width: 20%;
  border: 1px solid #ffffff;
  border-top: none;
  border-bottom: none;
}

#pc-box {
  flex: 1;
  flex-direction: column;
  /* background-image: url("https://archives.bulbagarden.net/media/upload/6/6f/Box_Distortion_Platinum_BDSP.png"); */
  /* background-size: 100%; */
  /* background-position: cover; */
  /* background-repeat: no-repeat; */
  z-index: 0;
  /* cursor: url("./image/pokemon-pc-box-hand.cur"), auto; */
}

#bgImg {
  display: flex;
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: -1;
}

.create-app,
.delete-app,
.folder {
  width: 3%;
  height: 7%;
  margin: 1%;
  margin-left: 2%;
}

#create-app-svg,
#delete-app-svg,
#folder-app-svg {
}

.app-description {
  display: flex;
  justify-self: center;
  align-self: center;
}

#folder-svg {
  fill: rgba(175, 175, 55, 1);
}

#delete-app {
  /* fill: rgba(25, 25, 25, 1); */
  /* stroke: #ffffff; */
}

@keyframes scale-out {
  0% {
    transform: scaleX(100%) scaleY(100%);
  }
  100% {
    transform: scaleX(200%) scaleY(0%);
  }
}

@keyframes scale-in {
  0% {
    transform: scaleX(200%) scaleY(0%);
  }
  100% {
    transform: scaleX(100%) scaleY(100%);
  }
}

@keyframes stretch {
  0% {
    transform: scaleY(1);
  }
  50% {
    transform: scaleY(1.1);
  }
  100% {
    transform: scaleY(1);
  }
}

@keyframes grow-red {
  0% {
    transform: scaleY(1);
    color: black;
  }
  50% {
    transform: scaleY(1.1);
    color: red;
  }
  100% {
    transform: scaleY(1);
    color: black;
  }
}

@keyframes fade-out-red {
  0% {
    opacity: inherit;
    color: black;
  }
  100% {
    opacity: 0%;
    color: red;
  }
}

.explorer-window {
  display: flex;
  justify-self: center;
  align-self: center;
  display: grid;
  height: 50%;
  width: 70%;
  grid-template-rows: 5% 95%;
  grid-template-columns: 20% 80%;
  border-radius: 5%;
  background-color: transparent;
  /* animation: scale-in 3s infinite; */
}

.explorer-header {
  grid-column: 1 / 3;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: white;
  filter: drop-shadow(0px -10px 10px #000000a1);
  border-top-right-radius: 1%;
  z-index: 2;
}

.explorer-sidebar {
  grid-row: 2 / 3;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-self: flex-start;
  justify-content: flex-start;
  align-items: flex-start;
  z-index: 1;
  background-color: rgba(255, 255, 255, 1);
  filter: drop-shadow(-10px 0px 10px #000000a1);
  border-bottom-left-radius: 1%;
}

.explorer-content {
  /* grid-row: 2 / 3; */
  /* width: 100%; */
  /* height: 100%; */
  /* display: flex; */
  /* flex-direction: column; */
  border-bottom-right-radius: 1%;
  filter: drop-shadow(10px 10px 10px #000000a1);
  background-color: var(--input-header-color);
}

.task-options {
  display: flex;
  width: 100%;
  height: 20%;
  padding: 5%;
}

.task-text {
  display: flex;
  background-color: var(--input-header-color);
  height: 40%;
  width: 60%;
  justify-content: center;
  align-items: center;
  border-radius: 15%;
}

#explorer-header-svg-close {
  display: flex;
  height: 75%;
  stroke: black;
  fill: rgba(220, 50, 70, 1);
  opacity: 80%;
  filter: drop-shadow(0px 10px 50px #000000a1);
  padding-right: 1%;
}

#explorer-header-svg-resize {
  display: flex;
  height: 75%;
  opacity: 80%;
  align-self: flex-start;
  transform: rotate(180deg);
}

.task-container {
  display: flex;
  width: 33%;
  height: 25%;
  font-size: 1em;
  border: 0.5em solid transparent;
  border-radius: 5%;
}

.task-inner {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.task-input-prompt,
.task-name {
  display: flex;
  text-wrap: wrap;
  font-size: 0.9em;
  background-color: rgba(150, 150, 150, 0.8);
  justify-content: center;
}

.task-form {
  height: 100%;
  background-color: #ffffff;
}

.task-input-form {
  background-color: #ffffff;
  display: flex;
  flex-direction: column;
}

.task-name-input,
.task-description-input,
.task-date-input,
.task-submit,
.task-description {
}

.task-date-input {
  width: 70%;
  justify-content: center;
  background-color: #ffffff;
}

.task-date-input {
  display: flex;
  justify-content: flex-start;
  font-size: 0.7em;
  width: 70%;
}

.task-input-form-bottom,
.form-bottom {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.task-description {
  display: flex;
  justify-content: flex-start;
  text-align: left;
  text-indent: 0.5em;
}

.form-bottom {
  display: flex;
  margin-top: 1%;
  justify-content: space-around;
}

.task-submit {
  display: flex;
  width: 30%;
  height: 70%;
  font-size: 0.5em;
  justify-content: center;
  align-items: center;
}

@keyframes close {
  0% {
  }
  100% {
    transform: scaleY(-900%) translateY(-200%) translateX(200%);
  }
}

@keyframes open {
  0% {
    transform: scaleY(-100%) scaleX(-100%) translateY(900%) translateX(900%);
  }
  100% {
  }
}

@keyframes fade-green {
  0% {
  }
  50% {
    background-color: emerald;
  }
  100% {
  }
}

@keyframes grow {
  0% {
  }
  50% {
    background-color: green;
    opacity: 95%;
    transform: scale(1.1);
  }
  100% {
  }
}

@keyframes fade {
  0% {
    opacity: 0%;
  }
  100% {
    opacity: 100%;
  }
}

@keyframes select {
  0% {
    border-color: transparent;
    border-radius: 5%;
  }
  100% {
    border-color: rgba(150, 245, 255, 0.8);
    border-radius: 5%;
  }
}

.new-folder-prompt-container {
  z-index: 2;
  flex-direction: row;
  justify-self: center;
  align-self: center;
  margin: 2%;
  width: 20%;
  height: 15%;
  font-size: 1em;
  border-radius: 1%;
}

.new-folder-prompt {
  width: 100%;
  height: 100%;
}

.text-prompt {
  justify-content: space-between;
  padding: 0.5%;
  align-items: center;
  width: 100%;
  height: 15%;
  background-color: #ffffff;
}

.folder-form {
  width: 100%;
  height: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--input-header-color);
}

.folder-name-input {
  margin-left: 1%;
  width: 70%;
}

.folder-submit-input {
  width: 5em;
  align-self: center;
  margin-left: 1%;
  margin-right: 1%;
  font-size: 75%;
}

#prompt-close-icon {
  display: flex;
  justify-self: right;
  height: 100%;
  stroke: black;
  fill: rgba(220, 50, 70, 1);
  opacity: 80%;
  filter: drop-shadow(0px 10px 50px #000000a1);
  padding-right: 1%;
}

